<template>
  <v-app>
    <v-row>
      <v-col>
        <v-btn @click="nextDay">UP</v-btn>
        <h1>{{ date.day }}</h1>
        <v-btn @click="prevDay">DOWN</v-btn>
      </v-col>
    </v-row>
  </v-app>
</template>

<script>
import dayjs from "dayjs"

export default {
  data() {
    return {}
  },
  methods: {
    calculateDate(count) {
      return dayjs(this.$store.state.date.day).add(count, 'd').format("YYYY-MM-DD")
    },
    nextDay() {
      this.$store.dispatch('increase',{
        day : this.calculateDate(1)
      })
    },
    prevDay() {
      this.$store.dispatch('decrease',{
        day : this.calculateDate(-1)
      })
    }
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  }
}
</script>

