<template>
  <v-app>
    <v-row>
      <v-col>
        <h1 style="margin: auto 0">{{ date.day }}</h1>
        <custom-calendar />
      </v-col>

      <v-col cols="11" sm="5">
        <v-menu ref="menu" v-model="menu2" :close-on-content-click="false" :nudge-right="40" :return-value.sync="time"
          transition="scale-transition" offset-y max-width="290px" min-width="290px">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field v-model="time" label="Picker in menu" prepend-icon="mdi-clock-time-four-outline" readonly
              v-bind="attrs" v-on="on"></v-text-field>
          </template>
          <v-time-picker v-if="menu2" v-model="time" full-width @click:minute="$refs.menu.save(time)"></v-time-picker>
        </v-menu>
      </v-col>
    </v-row>
  </v-app>
</template>

<script>
import customCalendar from "@/components/customCalendar.vue";
export default {
  data() {
    return {
      time: null,
      menu2: false,
      modal2: false,
    }
  },
  components: {
    customCalendar
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  },
}
</script>

