import Vue from 'vue'
import VueRouter from 'vue-router'
import main from '../views/mainPage.vue'
import subCalendarPage from '../views/subCalendarPage.vue'
import movieApiPage from '../views/movieApiPage.vue'
import CWM from '../views/CWM.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'main',
        component: main
    },
    {
        path: '/pageAnimation1',
        name: 'subCalendarPage',
        component: subCalendarPage
    },
    // {
    //     path: '/pageAnimation2',
    //     name: 'subCalendarPage',
    //     // route level code-splitting
    //     // this generates a separate chunk (about.[hash].js) for this route
    //     // which is lazy-loaded when the route is visited.
    //     component: subCalendarPage
    // },
    {
        path: '/movieApiPage',
        name: 'movieApiPage',
        component: movieApiPage
    },
    {
        path: '/CWM',
        name: 'CWM',
        component: CWM
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
