import Vue from "vue";
import Vuex from "vuex";
import dayjs from "dayjs";

Vue.use(Vuex);
export  default new Vuex.Store({
    state: {
        date: {
            day: dayjs().format("YYYY-MM-DD"),
        },
        pagelink: {
            page: 'pageAnimation',
        }
    },
    mutations: {
        addDay(state,payload) {
            state.date.day = payload;
        },
        addMainPost(state, payload) {
            state.date.day = payload;
        },
        dateIncrease(state, payload) {
            state.date.day = payload.day
        },

        dateDecrease(state, payload) {
            state.date.day = payload.day
        },
    },
    actions: {
        add({ commit }, payload) {
            commit('addMainPost', payload);
        },
        increase({commit}, payload) {
            commit('dateIncrease', payload);
        },
        decrease({commit}, payload) {
            commit('dateDecrease', payload);
        }
    },

    getters: {

    },
});